

function selectionSort(array){
    let size = array.length;
    for (let step = 0; step < size - 1; step++) {
      let min_idx = step

      for (let i = step + 1; i < size; i++) {
        if (array[i] < array[min_idx]) {
          min_idx = i;
        }
      }
      let temp = array[step];
      array[step] = array[min_idx];
      array[min_idx] = temp;
    }
    return array
}

export default selectionSort